.visually-hidden {
  z-index: -1;
  width: 0;
  height: 0;
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-igredients__form, .modal-ingredients__span, .modal-ingredients__item, .modal-ingredients__list {
