@keyframes top-to-bottom {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }

  50% {
    opacity: .6;
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bottom-to-top {
  from {
    opacity: 0;
    transform: translateY(150px);
  }

  50% {
    opacity: .6;
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes left-to-right {
  from {
    opacity: 0;
    transform: translateX(-250px);
  }

  50% {
    opacity: .6;
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

