.feedback .dots {
  width: 100%;
  z-index: 3333;
  height: 12px;
  justify-content: center;
  gap: 18px;
  display: flex;
  position: absolute;
  bottom: 60px;
  left: 0;
}

.feedback .dots .dot {
  width: 12px;
  height: 12px;
  cursor: pointer;
  -o-transition: all .5s ease-in-out;
  background: #d9d9d9;
  border-radius: 50%;
  list-style-type: none;
  transition: all .5s ease-in-out;
  display: inline-block;
  position: relative;
}

.feedback .dots .dot.active, .feedback .dots .dot:hover {
  background: #84a178;
}

.feedback .dots .dot.active {
  -ms-animation: feedback-scale .5s ease-in-out forwards;
  animation: feedback-scale .5s ease-in-out forwards;
}

.feedback .cont {
  position: relative;
}

.feedback .cont > div {
  text-align: center;
  opacity: 0;
  padding: 0 0 70px;
  position: absolute;
  top: 0;
  left: 0;
}

.feedback .cont > div.inactive {
  opacity: 0;
}

.feedback .cont > div.active {
  opacity: 1;
  position: relative;
}

.feedback .cont div .img img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: auto;
  display: block;
}

.feedback .cont div h2 {
  text-align: center;
  color: #131313;
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.86;
}

@media screen and (min-width: 768px) {
  .feedback .cont div h2 {
    font-size: 16px;
  }
}

.feedback .cont div p {
  margin: 32px auto;
}

.feedback .cont div.active .img img {
  -ms-animation: feedback-show .5s ease-in-out forwards;
  animation: feedback-show .5s ease-in-out forwards;
}

.feedback .cont div.active h2 {
  -ms-animation: feedback-content-in .4s ease-in-out forwards;
  animation: feedback-content-in .4s ease-in-out forwards;
}

.feedback .cont div.active p {
  -ms-animation: feedback-content-in .5s ease-in-out forwards;
  animation: feedback-content-in .5s ease-in-out forwards;
}

.feedback .cont div.inactive .img img {
  -ms-animation: feedback-hide .5s ease-in-out forwards;
  animation: feedback-hide .5s ease-in-out forwards;
}

.feedback .cont div.inactive h2 {
  -ms-animation: feedback-content-out .4s ease-in-out forwards;
  animation: feedback-content-out .4s ease-in-out forwards;
}

.feedback .cont div.inactive p {
  -ms-animation: feedback-content-out .5s ease-in-out forwards;
  animation: feedback-content-out .5s ease-in-out forwards;
}

@-webkit-keyframes feedback-scale {
  0% {
    box-shadow: 0 0 #eee;
  }

  35% {
    box-shadow: 0 0 10px 5px #eee;
  }

  70% {
    box-shadow: 0 0 10px 5px #ea830e;
  }

  100% {
    box-shadow: 0 0 #ea830e;
  }
}

@-moz-keyframes feedback-scale {
  0% {
    box-shadow: 0 0 #eee;
  }

  35% {
    box-shadow: 0 0 10px 5px #eee;
  }

  70% {
    box-shadow: 0 0 10px 5px #ea830e;
  }

  100% {
    box-shadow: 0 0 #ea830e;
  }
}



@-o-keyframes feedback-scale {
  0% {
    -o-box-shadow: 0px 0px 0px 0px #eee;
    box-shadow: 0 0 #eee;
  }

  35% {
    -o-box-shadow: 0px 0px 10px 5px #eee;
    box-shadow: 0 0 10px 5px #eee;
  }

  70% {
    -o-box-shadow: 0px 0px 10px 5px #ea830e;
    box-shadow: 0 0 10px 5px #ea830e;
  }

  100% {
    -o-box-shadow: 0px 0px 0px 0px #ea830e;
    box-shadow: 0 0 #ea830e;
  }
}

@keyframes feedback-scale {
  0% {
    box-shadow: 0 0 #eee;
  }

  35% {
    box-shadow: 0 0 10px 5px #eee;
  }

  70% {
    box-shadow: 0 0 10px 5px #ea830e;
  }

  100% {
    box-shadow: 0 0 #ea830e;
  }
}

@keyframes feedback-content-in {
  from {
    opacity: 0;
    transform: translateY(100%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes feedback-content-out {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(-100%);
  }
}

@keyframes feedback-show {
  from {
    opacity: 0;
    transform: scale(0);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes feedback-hide {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(0);
  }
}

.overlay {
